
import XImg from 'vux/src/components/x-img/index.vue'

import WechatPlugin from 'vux/src/plugins/wechat/index.js'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'

import Vue from 'vue'
Vue.use(WechatPlugin)

export default {
components: {
XImg
},
methods: {
success (src, ele) {
console.log('success load', src)
const span = ele.parentNode.querySelector('span')
ele.parentNode.removeChild(span)
},
error (src, ele, msg) {
console.log('error load', msg, src)
const span = ele.parentNode.querySelector('span')
span.innerText = 'load error'
}
},
data () {
return {
list: [
this.$route.params.imageUrl
]
}
},
mounted(){




this.$store.dispatch({
type: "getWeixinConfig"
}).then(res =>{
Vue.wechat.config(res);
console.log(this.$wechat);
this.$wechat.ready(function(){
alert('ok');
this.$wechat.onMenuShareTimeline({
title: 'hello VUX',
link: 'http://binsee.github.io/wechat-php-sdk/',
imgUrl: 'http://binsee.github.io/wechat-php-sdk/img/author.jpg',
success:function(){
alert('分享成功')
},
cancel:function(){
alert('分享失败')
}
})
});
this.$wechat.error(function(){
alert('你妹');
});

});
}
}



