
import Divider from 'vux/src/components/divider/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'
import Selector from 'vux/src/components/selector/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import CellBox from 'vux/src/components/cell-box/index.vue'

import Uploader from 'vux-uploader'
import { mapState, mapActions, mapGetters } from "vuex";
export default {
components: {
Group,
Divider,
XButton,
Uploader,
Selector,
Cell,
CellBox
},
data () {
return {
}
},
methods: {
createPost() {
this.$store.dispatch({
type: "updatePost"
}).then(res=>{this.$router.push({ name: 'post', params: { imageUrl: res }})});
},
ad: function (formData) {
if (this.$vux && this.$vux.loading) {
this.$vux.loading.show('正在上传')
}
this.$store.dispatch({
type: "updateImage",data:formData
}).then((response) => {

this.images.push(response.data.data)
})
}
},

computed: {
...mapGetters([
'tag',
'post'
])
},
created(){
this.$store.dispatch({
type: "getTags"
}
);
this.$store.dispatch({
type: "getLocationAndWeather"
}
);
},
props: {
images: {
type: Array,
default: () => []
},
varmax: {
type: Number,
default: 9
},
showHeader: {
type: Boolean,
default: true
},
showTip: {
type: Boolean,
default: true
},
readonly: {
type: Boolean,
default: false
},
title: {
type: String,
default: '图片上传'
},

handleClick: {
type: Boolean,
default: true
},

autoUpload: {
type: Boolean,
default: false
},
uploadUrl: {
type: String,
default: "http://114.215.134.80/api/image"
},
size: {
type: String,
default: 'normal'
},
capture: {
type: String,
default: ''
},
name: {
type: String,
default: 'img'
},
params: {
type: Object,
default: null
}
},
}



